import React from 'react';
import styled from 'styled-components';

const StyledStats = styled.ul`
	max-width: 900px;
	margin: 0 auto;
	display: flex;
`;

const UserStats = props => (
	<div>
		<StyledStats>
			<p>{props.itemsCount}</p>
			<p>{props.me.name}</p>
			<p>{props.me.email}</p>
		</StyledStats>
	</div>
);

export default UserStats;
